* {
	box-sizing: border-box;
}

body {
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
	margin: 0 auto;
	background-color: $c5;
	color: white;
	letter-spacing: 0.1rem;
	line-height: 1.5rem;
}

.dark-mode {
	transition: all 0.7s;
	background-color: $c5;
	color: white;

	.inputs,
	.stats-home-page,
	.stats li {
		background-color: $c5;
	}
}
.light-mode {
	transition: all 0.5s;
	min-height: 100vh;
	background-color: rgb(245, 245, 245);
	color: $c5;

	.stats-home-page,
	.stats li {
		background-color: rgb(245, 245, 245);
	}
	.inputs {
		background-color: rgba(230, 230, 230, 0.465);
	}
}

h1 {
	margin: 0;
}
