.form {
	padding-top: 20px;
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	align-items: center;
	.user-input {
		flex-direction: column;
		align-items: center;
		border-radius: 8px;
		padding: 0 15px;
		& div {
			label {
				display: flex;
				align-items: center;
				justify-content: space-between;
				font-weight: bold;
				font-size: 1.2rem;
				input {
					height: 30px;
					border-radius: 7px;
				}
				div {
					align-items: center;
				}
				.innrt-input {
					width: 170px;
					input {
						width: 100%;
					}
				}
			}
		}
	}
	button {
		margin: 10px 0;
		width: 100px;
		height: 30px;
		border-radius: 12px;
		cursor: pointer;
		border: 0;
		&:hover {
			background-color: $c2;
			border: 4px solid $c4;
		}
	}
	.pinging {
		cursor: not-allowed;
		background-color: $c3;
		&:hover {
			border: unset;
		}
	}
	textarea {
		border-radius: 7px;
		height: 30vh;
		width: 40vw;
	}
}
